@import url('https://fonts.googleapis.com/css2?family=PT+Sans&display=swap');
@import url('https://fonts.googleapis.com/css2?family=PT+Sans:wght@700&display=swap');

html,
body,
#root {
	height: 100%;
	overflow: hidden;
}

body {
	margin: 0;
	font-family: 'PT Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
	font-size: 15px;
	letter-spacing: 0.5px;
	color: rgb(75, 75, 75);
	-webkit-font-smoothing: subpixel-antialiased;
	-moz-osx-font-smoothing: grayscale;
	transition: all 0s;
}

* {
	box-sizing: border-box;
	user-select: none;
	transition:
		width ease-in-out 0.2s,
		height ease-in-out 0.2s,
		max-width ease-in-out 0.2s,
		max-height ease-in-out 0.2s,
		top ease-in-out 0.2s,
		bottom ease-in-out 0.2s,
		left ease-in-out 0.2s,
		right ease-in-out 0.2s,
		color ease-in-out 0.2s,
		background-color ease-in-out 0.2s,
		background-image ease-in-out 0.2s,
		border ease-in-out 0.2s,
		outline ease-in-out 0.2s,
		margin ease-in-out 0.2s,
		padding ease-in-out 0.2s;
}

h1 {
	font-size: 18px;
	text-transform: uppercase;
	letter-spacing: 3px;
	margin: 10px 0;
	font-weight: 600;
}

h2 {
	font-size: 15px;
	text-transform: uppercase;
	letter-spacing: 2.5px;
	margin: 10px 0 5px 0;
	font-weight: 600;
}

p {
	font-size: 15px;
	margin: 15px 0;
	text-transform: none;
	font-weight: normal;
}

b {
	font-weight: 600;
}

ul,
ol,
dl {
	padding-inline-start: 20px;

	li {
		margin: 5px 0;
	}
}

code {
	border: 1px solid rgb(220, 220, 220);
	border-radius: 3px;
	background-color: rgb(240, 240, 240);
	padding: 0 3px 2px 3px;
	font-family: unset;
}

hr {
	border: none;
	border-top: 2px solid rgb(220, 220, 220);
	height: 0;
	margin: 30px 0;
}

button {
	margin: 10px 0;
	padding: 6px 10px;
	min-height: 40px;
	font-family: 'PT Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
	font-size: 15px;
	text-transform: uppercase;
	letter-spacing: 2.5px;
	color: rgb(220, 220, 220);
	font-weight: 600;
	width: 100%;
	border: 2px solid rgb(220, 220, 220);
	border-radius: 5px;
	background-color: rgb(255, 255, 255);
	display: flex;
    flex-direction: column;
	align-items: center;
    justify-content: center;

	&.icon-btn {
		aspect-ratio: 1 / 1;
		margin: 0;
		border: none;
		border-radius: 10px;
		background-color: transparent;
		color: rgb(90, 90, 90);
		padding: 0;
	}

	&.checked {
		border: 2px solid rgb(60, 170, 255);
		background-color: rgb(200, 230, 255);
	}

	&:not(:disabled) {
		border-color: rgb(60, 170, 255);
		color: rgb(75, 75, 75);
		cursor: pointer;

		&:hover {
			background-color: rgb(200, 230, 255);
		}
	}

	&.danger:not(:disabled):hover {
		border-color: red;
		background-color: rgb(255, 200, 200);
	}
}

.developer {
	background-image: repeating-linear-gradient(-45deg, rgba(255, 0, 0, 0.2), rgba(255, 0, 0, 0.2) 5px, rgba(0, 0, 0, 0) 5px, rgba(0, 0, 0, 0) 10px);
}

.render-error {
	background-image: repeating-linear-gradient(-45deg, rgb(255, 255, 0), rgb(255, 255, 0) 10px, rgb(0, 0, 0) 10px, rgb(0, 0, 0) 20px) !important;
}

.inset-text {
	background-color: rgb(100, 100, 100);
	background-clip: text;
	color: transparent;
	text-shadow: rgba(255, 255, 255, 0.5) 2px 2px 5px;
}
